.woocommerce .woocommerce-breadcrumb{color:var(--dark-green);margin:0;padding:16px 0}.woocommerce .woocommerce-breadcrumb a{color:var(--gray);text-decoration:none;position:relative}.woocommerce .woocommerce-breadcrumb a::after{content:">";margin:0 8px}#breadcrumbs,#primary .woocommerce-breadcrumb{margin:0;padding:16px 0;font-size:var(--subtitle-font-size);font-weight:var(--subtitle-font-weight);line-height:var(--subtitle-line-height)}@media screen and (max-width: 768px){#breadcrumbs,#primary .woocommerce-breadcrumb{display:none}}#breadcrumbs .breadcrums-link-text,#primary .woocommerce-breadcrumb .breadcrums-link-text{font-family:"Inter",sans-serif}#breadcrumbs span a,#primary .woocommerce-breadcrumb span a{color:var(--gray);text-decoration:none;position:relative;font-family:"icomoon" !important;speak:never;font-style:normal;font-weight:normal;font-variant:normal;text-transform:none;line-height:1;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}#breadcrumbs span a::after,#primary .woocommerce-breadcrumb span a::after{content:"";color:var(--gray);margin:0 8px}#breadcrumbs .breadcrumb_last,#primary .woocommerce-breadcrumb .breadcrumb_last{color:var(--dark-green)}#primary .woocommerce-breadcrumb{padding:0 0 32px 0}