*,*::before,*::after{box-sizing:border-box}*{margin:0}body{-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-family:"Inter",sans-serif}img,picture,video,canvas,svg{display:block;max-width:100%}p,h1,h2,h3,h4,h5,h6{overflow-wrap:break-word}input[type=number]::-webkit-inner-spin-button,input[type=number]::-webkit-outer-spin-button{-webkit-appearance:none;-moz-appearance:none;appearance:none;margin:0}input[type=number]{-moz-appearance:textfield}a.remove_from_wishlist.button{display:none !important}.woocommerce-info{border-top:none;background-color:unset;color:var(--dark-green)}.woocommerce-info::before{display:none}.woocommerce ul.products li.product.type-product:hover{box-shadow:0px 4px 16px 0px rgba(0,0,0,.1215686275);padding:10px}.woocommerce ul.products li.product .price del,.woocommerce div.product p.price del{color:var(--red);opacity:1;font-size:var(--price-font-size);font-weight:var(--price-font-weight);line-height:var(--price-line-height);letter-spacing:var(--price-letter-spacing)}.woocommerce ul.products li.product .price ins,.woocommerce div.product p.price ins{text-decoration:none}#main div.product .out-of-stock{color:var(--main-color)}.woocommerce div.product form.cart{margin-bottom:0}.fa.fa-heart-o,.fa.fa-heart,.fa.fa-heart-o-white{margin:0;font-size:xx-large;font-family:"icomoon" !important;speak:never;font-style:normal;font-weight:normal;font-variant:normal;text-transform:none;line-height:1;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}.fa.fa-heart:before{content:"";color:#6fbc7b}.fa.fa-heart-o:before{content:"";color:#acacac}.fa.fa-heart-o-white:before{content:"";color:#fff}.woocommerce a.remove{color:var(--dark-grey) !important}#primary{position:relative}@media screen and (max-width: 768px){#primary{margin-top:119px}}